<template>
  <!-- 藏品详情 -->
  <div class="page">
    <div class="top-wrap">
      <div class="top-icon">
        <img
          src="~@/assets/images/collection/shop_top@2x.png"
          @click="viewImg"
        />
        <div class="info">
          <div class="tit">小雪-信小福</div>
          <div class="code">No.274895</div>
          <div class="share" @click="goShare">
            <img src="~@/assets/images/collection/share@2x.png" alt="" />
          </div>
        </div>
      </div>
    </div>
    <div class="cont-wrap">
      <div class="cont">
        <div class="group">
          <div class="text">持有者：张**</div>
          <div class="text">认证时间：2022-02-10 12:2</div>
        </div>
        <div class="group2">
          <div class="group-tit">藏品标识</div>
          <div class="group-cont">
            藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识
          </div>
        </div>

        <div class="group2">
          <div class="group-tit">藏品标识</div>
          <div class="group-cont">
            藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识藏品标识
          </div>
        </div>
        <div class="copyright">恒生·范太链提供技术支持</div>
      </div>
    </div>

    <div class="mask-layer" v-show="isShowImg">
      <div class="mask-con" @click="isShowImg = !isShowImg">
        <!-- <div class="content"> -->
        <img src="~@/assets/images/collection/shop_big@2x.png" />
        <!-- </div> -->
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      isShowImg: false
    }
  },
  methods: {
    goShare () {
      this.$router.push('digitalShare')
    },
    viewImg () {
      this.isShowImg = true
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  background-color: #fae2c8;
  min-height: 100%;
  .top-wrap {
    width: 100%;
    height: 500px;
    padding-top: 20px;
    .flex_func(column,flex-satrt,center,nowrap);
    .top-icon {
      width: 600px;
      height: 480px;
      position: relative;
      z-index: 2;
      .bg-image("~@/assets/images/collection/detail_bg","png");
      .flex_func(column,flex-satrt,center,nowrap);
      > img {
        width: 590px;
        height: 385px;
      }
      .info {
        width: 100%;
        height: 60px;
        line-height: 40px;
        padding: 0 26px;
        .flex_func(row,flex-satrt,center,nowrap);
        .tit {
          font-size: 30px;
          font-weight: 500;
          color: #5f2800;
        }
        .code {
          font-size: 26px;
          color: #ca8756;
          margin: 10px 40px 0 20px;
        }
        .share {
          width: 147px;
          height: 43px;
        }
        // background: pink;
      }
    }
  }
  .cont-wrap {
    width: 100%;
    min-height: 100%;
    padding: 0 15px 0;
    margin-top: -120px;
    .cont {
      width: 100%;
      min-height: 100%;
      padding-top: 120px;
      border-radius: 6px;
      background-image: linear-gradient(to bottom right, #f6ce9a, #fae2c4);
      .flex_func(column,flex-start,center,nowrap);
      .group {
        width: calc(100% - 50px);
        min-height: 136px;
        padding: 30px 33px;
        background: #fdf9f4;
        border-radius: 20px;
        // .bg-image("~@/assets/images/collection/card1","png");
        .text {
          width: 100%;
          height: 40px;
          text-align: left;
          font-size: 26px;
          color: #ca8756;
          line-height: 40px;
        }
      }
      .group2 {
        // width: 708px;
        width: calc(100% - 50px);
        min-height: 238px;
        padding: 24px 33px;
        margin-top: 50px;
        background: #fdf9f4;
        border-radius: 20px;
        // .bg-image("~@/assets/images/collection/card2","png");
        .flex_func(column,flex-start,center,nowrap);
        .group-tit {
          // width: 100%;
          width: 228px;
          height: 50px;
          margin-top: -35px;
          text-align: center;
          font-size: 34px;
          color: #99362a;
          line-height: 50px;

          .bg-image("~@/assets/images/collection/group_tit2","png");
        }
        .group-cont {
          width: 100%;
          padding: 20px 28px;
          font-size: 28px;
          color: #9d2e10;
          line-height: 40px;
        }
      }
      .copyright {
        width: 100%;
        text-align: center;
        font-size: 24px;
        color: #9d2e10;
        line-height: 33px;
        margin: 20px 0 40px;
        height: 40px;
      }
    }
  }
}
.mask-layer {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.45);
  z-index: 6;
  position: fixed;
  top: 0;
  left: 0;
  .mask-con {
    width: 100%;
    height: 100%;
    .flex_func(column, center, center, nowrap);
    position: relative;
    > img {
      width: 100%;
    }
  }
  .mask-btn {
    width: 100%;
    height: 70px;
    text-align: center;
    > img {
      width: 270px;
      height: 70px;
    }
  }
}
</style>



